import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Form,
  Button,
  Modal,
  Alert
} from "react-bootstrap";
import Loading from "./Loading";
import fire from "../config/Fire";

class AddRequest extends Component {
  constructor(props) {
    super(props);
    this.state = {
      description: "",
      error: ""
    };
  }
  handleSubmit = e => {
    e.preventDefault();
    this.setState({
      loading: true
    });
    console.log(fire.auth().currentUser.uid)
    this.writeData(this.props.category, this.state.description,fire.auth().currentUser.uid);
  };
  writeData = (category, description, user_id) => {
    fire
      .database()
      .ref("Request/")
      .push({
        category,
        description,
        user_id
      })
      .then(data => {
        //success callback
        console.log("data ", data);
        this.setState({
          loading: false
        },this.props.callback());
      })
      .catch(error => {
        //error callback
        console.log("error ", error);
        this.setState({
          loading: false,
          error:error
        });
      });
  };
  handleChange = e => {
    this.setState({ description: e.target.value });
  };
  render() {
    return (
      <Container>
        {this.state.loading ? <Loading show={this.state.loading} /> : ""}
        <Row>
          <Col>
            <Alert
              show={this.state.error.length > 0 ? true : false}
              dismissible
              variant="danger"
            >
              {this.state.error}
              <button
                type="button"
                className="close"
                aria-label="Close"
                onClick={() => this.dismissAlert()}
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </Alert>
          </Col>
        </Row>
        <Row>
          <Col>
            <Modal
              show={this.props.category ? true : false}
              onHide={this.props.callback}
            >
              <Modal.Header closeButton>
                <Modal.Title>Add Request</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Container>
                  <Row>
                    <Col>
                      <Form onSubmit={e => this.handleSubmit(e)}>
                        <Form.Group controlId="formBasicName">
                          <Form.Label>Category</Form.Label>
                          <Form.Control
                            type="text"
                            placeholder=""
                            defaultValue={this.props.category}
                          />
                        </Form.Group>
                        <Form.Group controlId="formBasicDescription">
                          <Form.Label>Description</Form.Label>
                          <Form.Control
                            as="textarea"
                            rows="3"
                            onChange={event => this.handleChange(event)}
                          />
                        </Form.Group>
                        <Button variant="primary" type="submit">
                          Submit
                        </Button>
                      </Form>
                    </Col>
                  </Row>
                </Container>
              </Modal.Body>
            </Modal>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default AddRequest;
