import React, { Component } from "react";
import fire from "../config/Fire";
import Categories from "./Categories";
import Auth from "./Auth";
import ListRequest from './ListRequest';
import {
  Container,
  Row,
  Col,
  Navbar,
  Jumbotron,
  Button,
  Modal,
  Nav
} from "react-bootstrap";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: null,
      loginModal: false
    };
    this.authListener = this.authListener.bind(this);
  }
  componentDidMount() {
    this.authListener();
  }
  authListener() {
    fire.auth().onAuthStateChanged(user => {
      console.log(user);
      if (user) {
        this.setState({ user });
        localStorage.setItem("user", user.uid);
      } else {
        this.setState({ user: null });
        localStorage.removeItem("user");
      }
    });
  }
  login = () => {
    console.log("login clicked");
    this.setState({
      loginModal: true
    });
  };
  dismissLoginModal = () => {
    this.setState({
      loginModal: false
    });
  };
  listRequest = () => {
    //this.writeUserData("dev@gmail.com", "dev", "jangir");
  }
  writeUserData(email,fname,lname){
    fire.database().ref('Users/').set({
        email,
        fname,
        lname
    }).then((data)=>{
        //success callback
        console.log('data ' , data)
    }).catch((error)=>{
        //error callback
        console.log('error ' , error)
    })
}
  loginHTML = () => {
    return (
      <Nav>
        <Navbar.Text>
          <span onClick={() => this.login()}>
            Log In
          </span>
        </Navbar.Text>
      </Nav>
    );
  };
  userHTML = () => {
    return (
      <Nav>
        <Navbar.Text>
          {this.state.user ? <a href="#">{fire.auth().currentUser.email.split("@")[0]}</a> : this.loginHTML()}
        </Navbar.Text>
      </Nav>
    );
  };
  render() {
    return (
      <React.Fragment>
        {this.state.loginModal ? (
          <Auth
            show={this.state.loginModal}
            callback={() => this.dismissLoginModal()}
          />
        ) : (
          ""
        )}
        <Container className="min-vh-100">
          <Row>
            <Col>
              <Navbar bg="warning">
                <Navbar.Brand href="#home">
                  <strong>E-Waste Recycle</strong>
                </Navbar.Brand>
                <Navbar.Toggle />
                <Navbar.Collapse className="justify-content-end">
                  {this.state.user ? this.userHTML() : this.loginHTML()}
                </Navbar.Collapse>
              </Navbar>
            </Col>
          </Row>
          <Row>
            <Col>
              <Jumbotron className="rounded-0 bg-light">
                <h1>E-Waste Recycle Process</h1>
                <p>
                  This is a simple hero unit, a simple jumbotron-style component
                  for calling extra attention to featured content or
                  information.
                </p>
                <p>
                  <Button variant="primary">Learn more...</Button>
                </p>
              </Jumbotron>
            </Col>
          </Row>
          <Row>
            <Col>
              <Categories />
            </Col>
            <Col>
              <ListRequest />
            </Col>
          </Row>
          <Row>
            <Col>
              <Jumbotron className="rounded-0 bg-warning">
                
              </Jumbotron>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}
export default Home;
