import React, { Component } from "react";
import { Container, Row, Col, Table } from "react-bootstrap";
import fire from "../config/Fire";
class ListRequest extends Component {
    constructor(){
        super();
        this.state = {
            request : null
        }
    }
    componentDidMount() {
        this.readUserData();
    }
    readUserData = () => {
        fire.database().ref('Request/').on('value', (snapshot) => {
            console.log(snapshot.val())
            let result = snapshot.val();
            this.setState({
                request : result
            })
            
            
        });
    } 
  render() {
    let result = this.state.request;
    return (
      <Container>
      <Row>
            <Col className="bg-dark">
              <div className="py-2 text-white">My Requests</div>
            </Col>
          </Row>
        <Row className="py-2">
          <Col>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Category</th>
                  <th>Description</th>
                </tr>
              </thead>
              <tbody>
                {result && Object.keys(result).map((key, index) => {
                    return (
                        <tr key={index}>
                  <td>{index + 1}</td>
                  <td>{result[key]["category"]}</td>
                  <td>{result[key]["description"]}</td>
                </tr>
                    )
                })}
              </tbody>
            </Table>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default ListRequest;
