import React, { Component } from "react";
import { Container, Row, Col, Modal, Button } from "react-bootstrap";
import Login from "./Login";
import Register from "./Register";

class Auth extends Component {
  constructor(props, context) {
    super(props, context);
    this.handleShow = this.handleShow.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.state = {
      loginMode: true
    };
  }
  handleClose() {
    this.props.callback();
  }
  handleShow() {
    this.props.callback();
  }
  updateFlow = () => {
      this.setState((prev) => {
            return {
                loginMode : !prev.loginMode
            }
      })
  }
  apiCallback = () => {
      this.props.callback();
  }
  render() {
    return (
      <Container>
        <Row>
          <Col>
            <Modal show={this.props.show} onHide={this.handleClose}>
              <Modal.Header closeButton>
                <Modal.Title>{this.state.loginMode ? "Login" : "Register" } </Modal.Title>
              </Modal.Header>
              <Modal.Body>
               {this.state.loginMode ? (<Login  callback={()=>this.apiCallback()} />) : (<Register callback={()=>this.apiCallback()} />)} 
              </Modal.Body>
              <Modal.Footer>
                <Button variant="success" onClick={() => this.updateFlow()}>
                {this.state.loginMode ? "SignUp" : "SignIn" } 
                </Button>
              </Modal.Footer>
            </Modal>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default Auth;
