import React, { Component } from "react";
import {
  CardDeck,
  Card,
  Container,
  Row,
  Col,
  CardGroup,
  Button
} from "react-bootstrap";
import AddRequest from './AddRequest';

class Categories extends Component {
constructor() {
    super();
    this.state = {
        category : null,

    }
}
  categoryClicked = (category) => {
        this.setState({
            category : category
        })
  }  
  dismissModal = () => {
      this.setState({
        category : null
      })
  }
  render() {
    return (
      <React.Fragment>
        <AddRequest category={this.state.category} callback={()=>this.dismissModal()} />
        <Container>
          <Row>
            <Col className="bg-dark">
              <div className="py-2 text-white">Recycle Categories</div>
            </Col>
          </Row>
          <Row>
            <Col md={4} lg={6} className="py-2">
            <Card >
                  <Card.Img variant="top" src={require('../elec-waste.jpg')} />
                  <Card.Body>
                    <Card.Title className="text-primary">Electronics</Card.Title>
                    <Card.Text />
                    <Button variant="success" onClick={() => this.categoryClicked("Electronics")}>Add Request</Button>
                  </Card.Body>
                </Card>
            </Col>
            <Col md={4} lg={6} className="py-2">
            <Card>
                <Card.Img variant="top" src={require('../plastic-waste.jpg')} />  
                  <Card.Body>
                    <Card.Title className="text-primary">Plastic</Card.Title>
                    <Card.Text />
                    <Button variant="success" onClick={() => this.categoryClicked("Plastic")}>Add Request</Button>
                  </Card.Body>
                </Card>
            </Col>
            <Col md={4} lg={6} className="py-2">
            <Card>
            <Card.Img variant="top" src={require('../paper-waste.jpg') } />  
                  <Card.Body>
                    <Card.Title className="text-primary">News Paper</Card.Title>
                    <Card.Text />
                    <Button variant="success" onClick={() => this.categoryClicked("News Paper")}>Add Request</Button>
                  </Card.Body>
                </Card>
            </Col>
            <Col md={4} lg={6} className="py-2">
            <Card>
            <Card.Img variant="top" src={require('../rubber-waste.jpg')} />  
                  <Card.Body>
                    <Card.Title className="text-primary">Rubber</Card.Title>
                    <Card.Text />
                    <Button variant="success" onClick={() => this.categoryClicked("Rubber")}>Add Request</Button>
                  </Card.Body>
                </Card>
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

export default Categories;
